/* --------------------------------

Externe links & Knoppen

-------------------------------- */

a[href^="http://"],
a[href^="https://"] {
  padding-right: 16px;
  background: url(../images/external-link-blue.png) no-repeat center right;
}

a[href^="mailto:"] {
  padding-right: 16px;
  background: url(../images/email-link-blue.png) no-repeat center right;
}

a.lichtgrijs {
  padding: 0;
  background: none;
}

/* Typeahead */

a.suggestionUrl,
a.suggestionUrl:hover {
  text-decoration: none;
  color: inherit;
}

a.suggestionUrl img.icon {
  position: absolute;
  left: 2px;
  width: 16px;
  height: 16px;
}

a:hover {
  text-decoration: underline;
}

.progress-bar-info {
  cursor: default;
}

.icon {
  vertical-align: text-bottom;
}

.module-icon {
  color: black;
  font-size: 1.7em;
  opacity: 0.2;
}

.module-icon:hover {
  opacity: 0.5;
}

input[type=submit]::-moz-focus-inner,
input[type=button]::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.btn.disabled,
button:disabled,
input[type=button]:disabled {
  color: #D3D3D3;
  border: 1px solid #D3D3D3 !important;
  cursor: default;
}

.disabled img {
  opacity: 0.3;
  filter: alpha(opacity=30);
}

.btn.loading {
  background-image: url(../images/loading-fb.gif);
  background-repeat: no-repeat;
  background-position: center center;
  color: transparent;
}

