/** FORMULIER **************************************************************/

.Formulier .field-required {
  color: red;
}

.Formulier .ui-tabs .ui-tabs-panel {
  padding: 0;
}

.Formulier .ui-tabs-vertical {
  padding: 0;
}

.Formulier .ui-tabs-vertical .ui-tabs-nav {
  float: left;
  width: 20%;
  padding: 2px 0 4px 3px;
}

.Formulier .ui-tabs-vertical .ui-tabs-nav li {
  clear: left;
  width: 100%;
}

.Formulier .ui-tabs-vertical .ui-tabs-nav li a {
  display: block;
  width: 100%;
}

.Formulier .ui-tabs-vertical .ui-tabs-panel {
  float: right;
  width: 80%;
  padding: 2em !important;
}

.Formulier select {
  padding: 0.4em 0.5em;
}

.Formulier input[type="number"] {
  padding: 0.1em .5em;
}

.Formulier .readonly {
  border: none;
  cursor: default;
}

.Formulier label .required {
  color: red;
}

.Formulier label .help {
  position: absolute;
  margin: 5px 0 0 -25px;
}

.Formulier .BBCodeField {
  height: 225px;
  border: none;
  padding: 5px;
  resize: none;
}

.Formulier textarea {
  width: 100%;
  line-height: 1.25em;
}

.Formulier .AutoSize {
  resize: none;
}

.Formulier .CsrBB {
  clear: left;
}

.Formulier .GroepAanmeldKnoppen {
  margin: 0;
}

.Formulier .InputField,
.Formulier h3,
.Formulier .WachtwoordField {
  clear: both;
}

.Formulier .InputField {
  border: 1px solid transparent;
}

.Formulier .InputField:hover {
  border: 1px solid #ddd;
  background-color: #eee;
}

.Formulier .CheckboxField {
  margin: 4px 0 0 4px;
}

.Formulier .CheckboxFieldLabel,
.Formulier .KeuzeRondjeLabel {
  float: none;
  width: auto;
}

.Formulier .previewDiv {
  display: inline-block;
  margin-left: 10px;
}

.Formulier .CsrBBPreview {
  float: left;
  clear: both;
  width: 100%;
}

.Formulier input.UidField {
  width: 4em;
}

.Formulier input.IntField,
.Formulier input.FloatField,
.Formulier input.BedragField,
.Formulier input.RequiredIntField,
.Formulier input.RequiredFloatField,
.Formulier input.RequiredBedragField {
  width: 5em;
  text-align: right;
}

.Formulier .InputField .waarschuwing {
  float: right;
  margin-right: 10px;
}

.Formulier .password {
  padding: 2px 1px;
}

.Formulier .CollapsableSubkopje {
  padding-left: 10px;
}

.Formulier .CollapsableSubkopje:hover {
  font-weight: bold;
}

/* Inline Formulier */

.InlineForm input[type="text"] {
  width: auto;
}

.InlineForm .InlineFormToggle {
  border-bottom: 1px dotted grey;
  width: 100%;
  min-height: 1em;
  cursor: url(../images/edit.png), text;
}

.InlineForm .InlineForm.ToggleForm {
  display: none;
}

.form-control.loading {
  background-image: url(../images/loading-fb.gif);
  background-repeat: no-repeat;
  background-position: right 40px center;
}

